body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-btn {
  width: 100%;
  height: 150px;
  text-align: center;
  padding: 50px;
  position: fixed;
  top: 30%;
}

button.pay-btn {
  padding: 6px 10px;
  color: #fff;
  background-color: #00672C;
  border: none;
  border-radius: 8px;
  height: 48px;
  font-size: 15px;
  width: 500px;
  margin: 0;
}

.sku-input {
  height: 40px;
  margin-bottom: 10px;
  width: 500px;
  padding: 10px;
  border: 1px solid #EBEBEB;
  border-radius: 8px;
  font-size: 15px;
}

@media screen and (max-width: 767px) {
  .sku-input {
    width: 100%;
  }

  button.pay-btn {
    width: 100%;
  }
}