/* TIMER STARTS */

/* body {
  font-family: sans-serif;
  display: grid;
  height: 100vh;
  place-items: center;
} */

.timer {
  display: flex;
  justify-content: center;
  padding-top: 40px;
}

.base-timer {
  position: relative;
  width: 200px;
  height: 200px;
}

.base-timer__svg {
  transform: scaleX(-1);
}

.base-timer__circle {
  fill: none;
  stroke: none;
}

.base-timer__path-elapsed {
  stroke-width: 2px;
  /* stroke: grey; */
}

.base-timer__path-remaining {
  stroke-width: 2px;
  stroke-linecap: round;
  transform: rotate(90deg);
  transform-origin: center;
  transition: 1s linear all;
  fill-rule: nonzero;
  stroke: currentColor;
}

.base-timer__path-remaining.green {
  /* color: rgb(65, 184, 131); */
  color: #074C8E;
}

.base-timer__path-remaining.orange {
  color: orange;
}

.base-timer__path-remaining.red {
  color: red;
}

.base-timer__label {
  position: absolute;
  width: 200px;
  height: 200px;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Gilroy-Medium';
  font-size: 24px;
  line-height: 140%;
  letter-spacing: 0.02em;
}

